import Link from 'next/link';

const styles = {
  display: 'flex',
  backgroundColor: 'grey',
  alignItems: 'center',
  justifyContent: 'space-between'
}

const Navbar = () => {
  return (
    <div style={styles}>
      <Link href='/about'>
        <button>About Page</button>
      </Link>
      <Link href='/contact'>
        <a>Contact Page</a>
      </Link>
      <h1>Index Page!!!</h1>
    </div>
  )
}

export default Navbar